import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import Json from '@/Json';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			searchName:'',
			searchId:'',//商品分类ID
			tabCurrentIndex: 0,
			types:'normal',
			//订单列表
			navList: [{
					state: 0,
					text: '综合',
				},
				{
					state: 1,
					text: '价格',
					
				},
				{
					state: 2,
					text: '销量',
					
				}
			],
			goodsList:[],//订单列表
			loadingType: 'more', //more 是加载中   nomore是加载结束
			total:0,//总条数
			pageSize:0,//每页条数
			page:1,//当前页
			tempNumber:0,
			nextPage:false,//是否有下一页
			
		};
	},
	
	async onLoad(option){
		
		let self = this;
		if(option.id){
			//点击分类进入
			self.searchId = option.id;
			self.searchName = option.name;
			
		}else{
			//点击搜索进到列表页面
			self.searchName = option.name;
			
		}
		//获取当前分类列表
		self.loadData();
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		// this.loadData();
	},
	
	methods: {
		//获取订单列表
		async loadData(){
			console.log(11111)
			let self = this;
			self.$axios(self.$baseUrl.getCategoryList,{
				id:self.searchId,
				page:self.page,
				types:self.types,
				name:self.searchName
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.goodsList = result.data;
					}else{//加载更多
				        result.data.forEach((item)=>{
							self.goodsList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
				
			},(error) =>{
				console.log(error)
			})
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//禁止手指滑动
		stopTouchMove: function() {
		  return false;
		},

		
		//顶部tab点击
		
		tabClick(index){
			this.tabCurrentIndex = index;
			switch(index){
				case 0:
		            this.types = 'normal';
					break;
				case 1:
				    this.types = 'price';
					break;
				case 2:
				    this.types = 'sales';
					break;
				default:
					break;
			}
			//获取当前分类列表,初始化数据
			this.goodsList = [];
			this.page = 1;
			this.total = 0;
			this.pageSize = 0;
			this.moreList = false;
			this.loadData();
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.goods_id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		
		//触发加载更多
		loadMore(){
			let self = this;
			if(self.nextPage){
				self.page = self.page +1;
				
				
				//调接口更新数据
				self.loadData();
			}
		}
    }
}
